// STYLES //////////////////////////////

body {
	font-family: 'open sans', 'san serif';
	margin: 0;

	h2,
	h4 {
		padding-left: 25px;
	}
}

hr {
	color: #000;
	border-top: 40px solid #000;
}

.itsagrid {
	margin: 35px 0;

	&.color .container {
		background-color: #D34224;
		position: relative;
		color: #fff;
	}

	&.full-width .container {
		background-color: #D34224;
		color: #fff;
	}

	.container .inner {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
	}
}

.container {
	padding: 20px;
	max-width: 700px;
	margin: 0 auto;

	.col {
		margin: 0 20px;
	}
}

